import datetime from '@/utils/datetime.js'
import {
	mapState
} from 'vuex'
import url from '@/request/url.js'
export default {
	data() {
		return {
			title: '',
			content: '',
			mail: '',
			noticeTime: datetime.getCurrentDate()
		}
	},
	computed: {
		...mapState({
			userInfo: state => state.userInfo
		})
	},
	methods: {
		submit() {
			uni.request({
				url: url.addLetter,
				method: "POST",
				data: {
					uid: this.userInfo.id,
					title: this.title,
					mail: this.mail,
					content: this.content,
					noticeTime: this.noticeTime
				},
				success: resp => {
					console.log(resp.data);
					uni.showToast({
						title: "未来信件已寄出",
						icon: "success"
					});
					uni.navigateBack({
						delta: 1
					});
				}
			});
		},
		dateChange(e) {
			this.date = e.detail.value
		}
	}
}
